  font-display: swap;
  font-style: normal;
  font-weight: 500;
}
body {
  font-family: "Oswald", "Arial", sans-serif;
  margin: 0;
  font-size: 14px;
  line-height: 30px;
  position: relative;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
