@media (min-width: 320px) {
  .profit__flex-wrapper {
    display: flex;
    flex-direction: column;
  }
  .profit__flex-block {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 90vw;
    height: 266px;
