    font-family: "Oswald", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 400;
    text-transform: uppercase;
  }
  .profit__title.profit__title--first {
    position: relative;
  }
  .profit__title.profit__title--second {
    position: relative;
  }
  .profit__title.profit__title--first::before {
    content: "";
    position: absolute;
    right: 150px;
    top: 0;
    width: 50px;
    height: 51px;
    background: no-repeat url("../img/sitting-panther.png") #68b738;
    background-size: 53% auto;
    background-position: 9px 4px;
    border: 1px solid #68b738;
    border-radius: 50%;
  }
  .profit__title.profit__title--second::before {
    content: "";
    position: absolute;
    right: 150px;
