    margin-left: 20px;
    padding: 0;
    text-transform: uppercase;
    color: #000000;
    position: relative;
  }
  .profit__link.profit__link::after {
    content: "";
    position: absolute;
    right: 3px;
    top: 10px;
    width: 24px;
    height: 11px;
    background: no-repeat url("../img/right-arrow.png");
    background-size: 100% auto;
  }
  .profit__link.profit__link::before {
    content: "";
