@media (min-width: 500px) {
  .example__text {
    width: 400px;
  }
  .example__calculation {
    margin-left: calc((100% - 300px)/2);
  }
}
@media (min-width: 600px) {
  .example__text {
    width: 430px;
  }
  .example__result-wrapper {
    max-width: 370px;
  }
}
@media (min-width: 768px) {
  .example {
    margin: 0;
    min-height: 580px;
    background: linear-gradient(to bottom, #fff 0, #fff 57%, #ededed 26%, #f6f6f6 64%, #f6f6f6 75%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#f6f6f6', GradientType=0);
  }
  .example__title {
    font-size: 60px;
  }
  .example__result-wrapper {
    width: 48%;
  }
  .example__wrapper-first {
    max-width: 800px;
    margin: 33px 0 44px calc((100% - 694px)/2);
  }
  .example__result-block {
    width: 162px;
  }
  .example__result-title {
    font-size: 14px;
    background-color: #ffffff;
    top: 46px;
  }
  .example__result-title--first {
    width: 112px;
  }
  .example__result-title--second {
    width: 145px;
  }
  .example__text {
    width: 674px;
    margin: 60px 0 56px 0;
    font-size: 16px;
    line-height: 1.5;
  }
  .example__calculation {
    margin: 0 0 0 200px;
    width: 155px;
    font-size: 20px;
  }
  .example__calculation-substring {
    display: block;
    margin: 0 0 0 68px;
  }
  .example__image-wrapper {
    margin: 0 0 0 calc((100% - 700px)/2);
    position: relative;
  }
  .example__image {
    display: unset;
  }
  .example__image.example__image--slim-cat {
    display: inline;
    position: absolute;
    top: 2px;
  }
  .example__controller {
    width: 428px;
    height: 2px;
    margin: 1px 0 2px 0;
    background-color: #dcdcdc;
    margin: -42px auto;
    display: block;
    position: relative;
  }
  .example__controller.example__controller::before {
    content: "БЫЛО";
    position: absolute;
    width: 36px;
    height: 15px;
    font-size: 20px;
    right: 445px;
    bottom: 2px;
  }
  .example__controller.example__controller::after {
    content: "СТАЛО";
    position: absolute;
    width: 36px;
    height: 15px;
    font-size: 20px;
    left: 445px;
    bottom: 0px;
  }
  .example__progressive-bar {
    width: 34px;
    height: 34px;
    box-shadow: 0px 2px 5px 0 rgba(0, 0, 0, 0.15);
    border: solid 1px #cdcdcd;
    border-radius: 50%;
    background-color: #ffffff;
    margin: -16px 0 0 12px;
    position: relative;
  }
  .example__progressive-bar.example__progressive-bar::before {
    content: "";
    width: 2px;
    height: 2px;
    position: absolute;
    background-color: grey;
    top: 15px;
    left: 15px;
  }
}
@media (min-width: 1440px) {
  .example {
    flex-direction: row;
    background: linear-gradient(to bottom, #fff 0, #fff 28%, #ededed 26%, #f6f6f6 64%, #f6f6f6 75%);
    justify-content: space-around;
    min-height: 580px;
  }
  .example__text {
    width: 800px;
    margin: 140px 0 80px 0;
  }
  .example__image-wrapper {
    position: relative;
  }
  .example__image {
    position: unset;
  }
  .example__image.example__image--slim-cat {
    top: -10px;
  }
  .example__result-wrapper {
    width: 84%;
  }
  .example__result-title {
    background-color: #f2f2f2;
  }
  .example__result-title.example__result-title--first {
    width: 110px;
  }
  .example__result-title.example__result-title--second {
    width: 100px;
  }
  .example__wrapper-first {
    max-width: 435px;
    margin: 0;
  }
  .example__calculation {
    margin: 45px 0 0 0;
    width: 320px;
  }
  .example__calculation-substring {
    display: unset;
  }
  .example__controller {
    margin-top: 48px;
  }
}
@media (min-width: 320px) {
  .map-wrapper {
    width: 100%;
    min-height: 360px;
    background: no-repeat url("../img/map-mobile.png");
    background-size: 100% 400px;
  }
  .map-wrapper__context {
    width: 100%;
    height: 400;
  }
}
@media (min-width: 320px) {
  .contacts {
    min-height: 115px;
    font-family: "Oswald", "Arial", sans-serif;
    display: flex;
    justify-content: space-around;
  }
  .contacts__title {
    width: 7rem;
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1.25;
  }
  .contacts__text {
    width: 10rem;
    font-size: 14px;
    font-family: "Arial", sans-serif;
    line-height: 1.43;
    letter-spacing: 1px;
  }
}
